
import { PlainObject } from '@/typings/interface'
import { defineComponent } from 'vue'
import { mapState } from 'vuex'

import SearchBar from './DataTableSearchBar.vue'

export default defineComponent({
  name: 'DataTable',
  components: {
    SearchBar,
  },
  props: {
    headers: {
      type: Array,
      default: (): PlainObject[] => [],
    },
    items: {
      type: Array,
      default: (): PlainObject[] => [],
    },
    includeAction: {
      type: Boolean,
      default: false,
    },
    searchField: {
      type: String,
      default: 'Full Part Name',
    },
  },
  emits: ['selected'],

  data: () => ({
    sortBy: '',
    sortDesc: true,

    pagination: {
      perPage: 10,
      page: 0,
    },

    checkPlayerLevel: true,
    searchQuery: '',
  }),

  computed: {
    ...mapState({
      playerLevel: 'playerLevel',
    }),

    currentPageText (): string {
      const { perPage, page } = this.pagination
      const total = this.parsedItems.length
      if (!total) return 'No Items'

      const start = (page * perPage) + 1
      const end = Math.min(total, (page + 1) * perPage)

      return `${start} - ${end} of ${total}`
    },

    currentlyDisplayedItems (): any[] {
      const { perPage, page } = this.pagination
      return this.parsedItems.slice(page * perPage, (page + 1) * perPage)
    },

    onFirstPage (): boolean {
      return this.pagination.page === 0
    },

    onLastPage (): boolean {
      const { perPage, page } = this.pagination
      const total = this.parsedItems.length

      return page === Math.floor(total / perPage)
    },

    parsedItems (): any[] {
      const filtered = this.items.slice().filter((item: any) => {
        let valid = true

        if (this.checkPlayerLevel) {
          const level = item.Level || item.level || 1
          valid = valid && level <= this.playerLevel
        }

        if (this.searchQuery) {
          const itemLowerCase = item[this.searchField].toLowerCase()
          const queryLowerCase = this.searchQuery.toLowerCase()
          valid = valid && itemLowerCase.includes(queryLowerCase)
        }

        return valid
      })

      if (!this.sortBy) return filtered

      return filtered.sort((a: any, b: any) => {
        if (a[this.sortBy] < b[this.sortBy]) return this.sortDesc ? -1 : 1
        if (a[this.sortBy] > b[this.sortBy]) return this.sortDesc ? 1 : -1
        return 0
      })
    },
  },

  methods: {
    toggleSort (name: string): void {
      if (this.sortBy === name) {
        if (!this.sortDesc) {
          this.sortBy = ''
        }

        this.sortDesc = !this.sortDesc
      } else {
        this.sortBy = name
        this.sortDesc = true
      }
    },
  },
})
